import { Component, ViewChild } from '@angular/core';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { EulaService } from './eula.service';
import { useLoader } from 'app/utils/loader/decorators';
import { AuthState } from 'app/services/auth-state';
import { LicenseState } from 'app/services/license-state';

@Component({
    selector: 'eula',
    templateUrl: './eula.component.html',
    styleUrls: ['./eula.component.scss'],
})

export class EulaComponent {
    @ViewChild(LoaderDirective, {static: true}) loader: LoaderDirective;
    eulaStatus: boolean = true;
    constructor(
        public eulaService: EulaService,
        public authState: AuthState,
        public licenseState: LicenseState,
    ) {}
    @useLoader
    public async accept() {
        await this.eulaService.accept();
    }
}
