import { Component, TemplateRef, ViewChild } from '@angular/core';
import { AppState } from 'app/core/app.service';
import { FeaturesManagementService } from 'app/services/features-management';
import {
    FeaturesManagementItem,
} from '@imunify360-api/features-management';
import { useLoader } from 'app/utils/loader/decorators';
import { RequestDirective } from 'app/utils/table/request/request.directive';
import { ModalService } from 'app/utils/modals/modal.service';
import { firstValueFrom, switchMap, tap, catchError, of } from 'rxjs';
import { LicenseState } from 'app/services/license-state';
import { Product } from '@imunify360-api/license';
import { GroupNotificationsService } from 'app/services/group-notifications';

@Component({
    selector: 'features-management-legacy',
    styleUrls: ['./legacy.component.scss'],
    templateUrl: './legacy.component.html',
})

export class FeaturesManagementLegacyComponent {
    Product = Product;
    packageName = IMUNIFY_PACKAGE;
    @ViewChild(RequestDirective, {static: true}) request: RequestDirective;
    @ViewChild('newFeaturesManagementModal', {static: true})
    public newFeaturesManagementModal: TemplateRef<any>;

    constructor(public appState: AppState,
                public licenseState: LicenseState,
                public featuresManagementService: FeaturesManagementService,
                private modalService: ModalService,
                private groupNotifications: GroupNotificationsService) {}

    @useLoader
    public async enableNative() {
        if (!await this.modalService.open(this.newFeaturesManagementModal)) {
            return;
        }
        await firstValueFrom(
            this.featuresManagementService.enableNative().pipe(
                switchMap(() => this.featuresManagementService.getNativeStatus()),
                catchError(() => of(null)),
            ),
            { defaultValue: undefined }
        );
    }

    @useLoader
    public async changeFeature(data: {
        state: boolean,
        status: string,
        items: FeaturesManagementItem[],
    }) {
        const action = !data.state ? 'enable' : 'disable';
        const userList = data.items
            .filter(x => x[`${data.status}`] === data.state)
            .map(x => x.name);
        if (!await this.modalService.confirm(`${action}-${data.status}`, userList)) {
            return;
        }
        await firstValueFrom(this.featuresManagementService.editUserFeatures({
            action: action,
            feature: data.status,
            items: userList,
        }).pipe(
            tap((result) => {
                this.groupNotifications.forAction('edit-user-features', result.data.items, {
                    feature: data.status,
                    action,
                });
            }),
            switchMap(() => this.request.load()),
        ), { defaultValue: undefined });
    }
}
