import { Component, ViewChild } from '@angular/core';
import { FeaturesManagementService } from 'app/services/features-management';
import { useLoader } from 'app/utils/loader/decorators';
import { FeaturesStatus } from '@imunify360-api/features-management';
import { RequestDirective } from 'app/utils/table/request/request.directive';
import { ModalService } from 'app/utils/modals/modal.service';
import { NotificationsService } from 'app/services/notifications';
import { MatSlideToggle } from '@angular/material/slide-toggle';
import { firstValueFrom, switchMap, tap } from 'rxjs';

@Component({
    selector: 'default-features',
    templateUrl: './default-features.component.html',
    styleUrls: ['./default-features.component.scss'],
})

export class DefaultFeaturesComponent {
    counts: FeaturesStatus = {
        proactive: false,
        av: false,
    };
    @ViewChild(RequestDirective, {static: true}) request: RequestDirective;

    constructor(public featuresManagementService: FeaturesManagementService,
                private modalService: ModalService,
                private notifications: NotificationsService) {
    }

    public async changeDefaultFeature(data: { feature: string },
                                      source: MatSlideToggle) {
        await setTimeout(() => {
            source.checked = !source.checked;
        }, 1);
        const action = source.checked ? 'enable' : 'disable';
        if (!await this.modalService.confirm(`${action}-${data.feature}-default`,
                [])) {
            return;
        }

        await this.editFeature(action, data.feature);
    }

    @useLoader
    private async editFeature(action: 'enable' | 'disable', feature: string): Promise<void> {
        await firstValueFrom(this.featuresManagementService.editDefaultFeature({
            action,
            feature,
        }).pipe(
            tap(() => {
                this.notifications.success(
                    `notifications.editUserFeatures.notifications.success`,
                    null,
                    {feature, action},
                );
            }),
            switchMap(() => this.request.load()),
        ), { defaultValue: undefined });
    }

}
