import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { EulaComponent } from './eula.component';
import { I360TranslateModule } from 'app/utils/translate/translate.module';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { I360LoaderModule } from 'app/utils/loader/loader.module';
import {
    I360BlockedUiMessageModule,
} from 'app/utils/blocked-ui-message/blocked-ui-message.module';
import { Route, RouterModule } from '@angular/router';
import { EulaGuard } from './eula.guard';

export const EULA_ROUTE: Route = {
    path: 'eula',
    loadChildren: () => import('../eula/eula.module').then(m => m.I360EulaModule),
    canActivate: [EulaGuard],
};

@NgModule({
    imports: [
        RouterModule.forChild([{
            path: '',
            pathMatch: 'full',
            component: EulaComponent,
        }]),
        CommonModule,
        I360TranslateModule,
        MatButtonModule,
        MatIconModule,
        I360LoaderModule,
        I360BlockedUiMessageModule,
    ],
    declarations: [EulaComponent],
    exports: [EulaComponent],
})
export class I360EulaModule {}
