import { Component, Input } from '@angular/core';

@Component({
    selector: 'i360-domains',
    template: `
        <ng-container>
            <div class="i360-domains">
                <span *ngFor="let item of items.slice(0, allDomains ? undefined : maxShownDomains)">
                    {{ item }}
                </span>
            </div>
            <div>
                <a *ngIf="items.length > maxShownDomains && !allDomains"
                   (click)="allDomains = !allDomains"
                   class="i360-domains-button">
                        {{ 'settings.featuresManagement.showDomains'
                            | translate: { size: items.length } }}
                </a>
                <a *ngIf="items.length > maxShownDomains && allDomains"
                   (click)="allDomains = !allDomains"
                   class="i360-domains-button">
                    {{ 'settings.featuresManagement.hideDomains' | translate }}
                </a>
            </div>
        </ng-container>
    `,
    styles: [`
        .i360-domains {
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-start;
            align-content: center;
        }
        .i360-domains>span {
            margin: 5px 5px 5px 0;
            padding-right: 10px;
        }
    `],
})
export class DomainsComponent {
    @Input() items: string[];
    allDomains = false;
    maxShownDomains = 3;
}


