import {
    AfterContentInit,
    ContentChild,
    Directive,
    ElementRef,
    OnDestroy,
} from '@angular/core';
import { fromEvent, Subscription } from 'rxjs';


@Directive({
    selector: '[i360-accordion]',
    host: {
        '[class.i360-accordioned]': 'true',
        '[class.i360-expended]': 'expended',
    },
})
export class AccordionDirective implements AfterContentInit, OnDestroy {
    onToggle = Subscription.EMPTY;
    expended = false;
    @ContentChild('trigger', {static: true}) trigger: ElementRef;

    ngAfterContentInit(): void {
        this.onToggle = fromEvent(this.trigger.nativeElement, 'click')
            .subscribe(() => {
                this.expended = !this.expended;
            });
    }

    ngOnDestroy(): void {
        this.onToggle.unsubscribe();
    }
}
