import { I360Validators } from '../validator/validators';
import { ConfigTypeExtractingHelper, PermissionsType } from './misc';
import { ScheduleInterval } from '@imunify360-api/settings';
import { I360FormBuilder } from 'app/utils/validator/form-builder';
import { Validators } from '@angular/forms';

export const getSchema = (formBuilder: I360FormBuilder) => {
    return {
        ERROR_REPORTING: formBuilder.group({
            enable: formBuilder.control(true),
        }),
        MALWARE_SCAN_SCHEDULE: formBuilder.group({
            interval: formBuilder.control<ScheduleInterval | null>({
                value: null,
                allowedValues: [{
                    value: ScheduleInterval.none,
                    title: 'settings.malware.scheduledScanning.interval.options.none',
                }, {
                    value: ScheduleInterval.day,
                    title: 'settings.malware.scheduledScanning.interval.options.day',
                }, {
                    value: ScheduleInterval.week,
                    title: 'settings.malware.scheduledScanning.interval.options.week',
                }, {
                    value: ScheduleInterval.month,
                    title: 'settings.malware.scheduledScanning.interval.options.month',
                }],
            }, I360Validators.required('schedule_type')),
            day_of_month: formBuilder.control<number>(null, [
                I360Validators.integer(true), Validators.min(1), Validators.max(31),
            ]),
            day_of_week: formBuilder.control<number | null>({
                value: null,
                allowedValues: Array.from({length: 7}, (v, i) => i),
            }, I360Validators.required('')),
            hour: formBuilder.control<number | null>({
                value: null,
                allowedValues: Array.from({length: 24}, (v, i) => i),
            }, I360Validators.required('')),
        }),
        MALWARE_CLEANUP: formBuilder.group({
            trim_file_instead_of_removal: formBuilder.control(true),
            keep_original_files_days: formBuilder.control(14,
                [I360Validators.integer(true), Validators.min(1)]),
        }),
        MALWARE_SCANNING: formBuilder.group({
            rapid_scan: formBuilder.control(false),
            sends_file_for_analysis: formBuilder.control(true),
            detect_elf: formBuilder.control(false),
            hyperscan: formBuilder.control(false),
        }),
        MALWARE_SCAN_INTENSITY: formBuilder.group({
            cpu: formBuilder.control(7),
            io: formBuilder.control(7),
        }),
        MY_IMUNIFY: formBuilder.group({
            enable: formBuilder.control(false),
            purchase_page_url: formBuilder.control<string>(''),
        }),
    };
};

const a = () => (null as any as I360FormBuilder).group(getSchema(null as any));
export type AntivirusConfigType = ConfigTypeExtractingHelper<typeof a> & PermissionsType;
