import { Injectable } from '@angular/core';
import {
    FeaturesManagementAPI,
    FeaturesManagementItem,
    FeaturesStatus,
    NativeFeaturesManagementStatus,
} from '@imunify360-api/features-management';
import { cache } from 'app/utils/cache';
import { Observable, Subject, map, tap } from 'rxjs';
import { XHR } from 'app/utils/xhr/xhr';

@Injectable()
export class FeaturesManagementService {
    constructor(public xhr: XHR) {}

    getNativeStatusRaw = this.xhr.rx(FeaturesManagementAPI.nativeStatus);
    enableNative = this.xhr.rx(FeaturesManagementAPI.nativeEnable);
    getAllowed = this.xhr.rx(FeaturesManagementAPI.allowed);
    getFeaturesManagement = this.xhr.wrap(this.xhr.rx(FeaturesManagementAPI.list),
        FeaturesManagementItem);
    getFeaturesManagementDefaults = this.xhr.rx(FeaturesManagementAPI.defaults);
    editUserFeatures = this.xhr.rx(FeaturesManagementAPI.editUserFeatures);
    editDefaultFeature = this.xhr.rx(FeaturesManagementAPI.editDefaultFeature);

    forceNativeStatusUpdate = new Subject<NativeFeaturesManagementStatus>();
    native: Observable<NativeFeaturesManagementStatus> = this.getNativeStatus().pipe(
        map(response => response.data.items),
        cache(300, this.forceNativeStatusUpdate),
    );
    allowed = this.getAllowed().pipe(
        map((res): FeaturesStatus => {
            return res.data.items.reduce((res, next) => {
                res[next] = true;
                return res;
            }, {}) as FeaturesStatus;
        }),
        cache(300),
    );

    getNativeStatus() {
        return this.getNativeStatusRaw().pipe(
            tap(response => this.forceNativeStatusUpdate.next(response.data.items)),
        );
    }
}

