import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { I360FormBuilder } from 'app/utils/validator/form-builder';
import { useLoader } from 'app/utils/loader/decorators';
import { Subject, filter, map, takeUntil, tap, firstValueFrom } from 'rxjs';
import { SettingsService } from 'app/services/settings';
import { NotificationsService } from 'app/services/notifications';
import { LoaderDirective } from 'app/utils/loader/loader.directive';
import { TranslateService } from 'app/utils/translate/translate.service';
import { I360Validators } from 'app/utils/validator/validators';
import { Validators } from '@angular/forms';
import { DEFAULT_KEY } from './notification-emails.component';
import { Package } from '@imunify360-api/license';
import { configsEqual } from 'app/utils/configs-equal';
import { testMode } from 'app/services/misc';


@Component({
    selector: 'settings-notification',
    templateUrl: './settings-notification.component.html',
})
export class SettingsNotificationComponent implements OnInit, OnDestroy {
    is360Package = IMUNIFY_PACKAGE === Package.imunify360;
    destroyed = new Subject<void>();
    localeExists: boolean = false;
    @ViewChild(LoaderDirective, {static: true}) public loader: LoaderDirective;
    public form = this.formBuilder.group(this.getSchema());
    originalConfig;

    public constructor(
        private formBuilder: I360FormBuilder,
        private settingsService: SettingsService,
        private notifications: NotificationsService,
        public translate: TranslateService,
    ) {}

    @useLoader
    async ngOnInit() {
        await firstValueFrom(this.settingsService.notificationShow().pipe(
            map(res => res.data.items),
            takeUntil(this.destroyed),
            filter(configFromBackend => configFromBackend !== undefined),
            map(configFromBackend => {
                const config = JSON.parse(JSON.stringify(configFromBackend));
                this.originalConfig = config;
                if ('admin' in config && 'locale' in config.admin) {
                    delete config.admin.locale;
                    this.localeExists = true;
                }
                for (let rule of Object.keys(config.rules)) {
                    if ('ADMIN' in config.rules[rule] && 'period' in config.rules[rule]['ADMIN']) {
                        config.rules[rule]['ADMIN']['period']
                            = Math.ceil(config.rules[rule]['ADMIN']['period'] / 60);
                    }
                }
                this.form.patchValue(config);
            }),
        ), { defaultValue: undefined });
    }

    confirmReload() {
        const original = this.originalConfig
        const current = this.normalizedForm(false);
        this.removeDefaults(original);
        this.removeDefaults(current);
        let confirmationNeeded = false;
        if (IMUNIFY_PACKAGE === Package.imunify360) {
            delete original?.admin?.locale;
            delete current?.admin?.locale;
            confirmationNeeded ||= !configsEqual({admin: original?.admin}, {admin: current?.admin});
        }
        // NOTE: `admin` and `rules` are checked separately due to limitations of `configsEqual`.
        // It only skips missing on one level.
        confirmationNeeded ||= !configsEqual(original.rules, current.rules);
        // split from `I360Config.formConfirmed`, because this component doesn't use `I360Config`
        // TODO: do not duplicate this logic
        return confirmationNeeded
            && !localStorage.getItem('I360_UNSAVED_CONFIG_LEAVING_PAGE_CONFIRM_DISABLED')
            && !testMode('SettingsNotificationComponent')
            && confirm(this.translate.translate.instant('reload-page'));
    }

    removeDefaults = (obj) => {
        Object.keys(obj || {}).forEach(key => {
            if (['scripts', 'admin_emails'].includes(key)
                    && Array.isArray(obj[key]) && !obj[key].length) {
                delete obj[key];
            }
            if (key === 'period' && obj[key] === 1) {
                delete obj[key];
            }
            if (obj[key] && typeof obj[key] === 'object') {
                this.removeDefaults(obj[key]);
            }
        })
    }

    ngOnDestroy() {
        this.destroyed.next();
    }

    private normalizedForm(normalizePeriod = true) {
        const notificationConfig = JSON.parse(JSON.stringify(this.form.value));
        if ('admin' in notificationConfig && !this.localeExists) {
            notificationConfig.admin.locale = this.translate.currentLang;
        }
        if (normalizePeriod) {
            for (const rule of this.adminGenerator()) {
                if ('period' in notificationConfig.rules[rule]['ADMIN']) {
                    notificationConfig.rules[rule]['ADMIN']['period'] *= 60;
                }
            }
        }
        return notificationConfig;
    }

    @useLoader
    async submit() {
        this.originalConfig = JSON.parse(JSON.stringify(this.form.value));
        await firstValueFrom(this.settingsService
            .saveNotificationSettings(this.normalizedForm()).pipe(
                tap(() => this.notifications.success(
                    'settings.successSaveMessage',
                    void 0,
                )),
            ), { defaultValue: undefined });
    }

    * adminGenerator() {
        const notificationConfig = JSON.parse(JSON.stringify(this.form.value));
        for (let rule of Object.keys(notificationConfig.rules)) {
            if ('ADMIN' in notificationConfig.rules[rule]) {
                yield rule;
            }
        }
    }

    private getSchema() {
        const periodControl = () => {
            return {
                period: this.formBuilder.control(1,
                    [
                        I360Validators.integer(true),
                        Validators.min(1),
                    ],
                ),
            };
        };

        const getScriptSchema = (period: boolean = false) => {
            return {
                SCRIPT: this.formBuilder.group(
                    {
                        scripts: this.formBuilder.control<string[]>([], [
                            I360Validators.required('script'),
                            I360Validators.listOfAbsolutePaths,
                        ]),
                        enabled: this.formBuilder.control(false),
                        ...(period ? periodControl() : {}),
                    },
                ),
            };
        };

        const getAdminSchema = (period: boolean = false) => {
            const enabled = this.formBuilder.control(false);
            const adminEmails = this.formBuilder.control<string[]>([], []);
            enabled.valueChanges.pipe(
                takeUntil(this.destroyed),
            ).subscribe(() => {
                if (enabled.value && !adminEmails.value.includes(DEFAULT_KEY)) {
                    adminEmails.setValue([...adminEmails.value, DEFAULT_KEY]);
                }
            });
            return {
                ADMIN: this.formBuilder.group(
                    {
                        admin_emails: adminEmails,
                        enabled: enabled,
                        ...(period ? periodControl() : {}),
                    },
                ),
            };
        };

        const getNotificationScanDetectFilds = (period: boolean = false) => {
            return {
                ...getAdminSchema(period),
                ...getScriptSchema(period),
            };
        };

        if (this.is360Package) {
            return {
                admin: this.formBuilder.group({
                    default_emails: this.formBuilder.control<string[]>([], [
                        I360Validators.emails,
                    ]),
                    notify_from_email: this.formBuilder.control<string>('', [
                        I360Validators.required('email'), I360Validators.email,
                    ]),
                }),
                rules: this.formBuilder.group({
                    REALTIME_MALWARE_FOUND: this.formBuilder.group(
                        getNotificationScanDetectFilds(true),
                    ),
                    USER_SCAN_STARTED: this.formBuilder.group(
                        getScriptSchema(),
                    ),
                    CUSTOM_SCAN_STARTED: this.formBuilder.group(
                        getScriptSchema(),
                    ),
                    USER_SCAN_FINISHED: this.formBuilder.group(
                        getScriptSchema(),
                    ),
                    CUSTOM_SCAN_FINISHED: this.formBuilder.group(
                        getScriptSchema(),
                    ),
                    CUSTOM_SCAN_MALWARE_FOUND: this.formBuilder.group(
                        getNotificationScanDetectFilds(false),
                    ),
                    USER_SCAN_MALWARE_FOUND: this.formBuilder.group(
                        getNotificationScanDetectFilds(false),
                    ),
                    SCRIPT_BLOCKED: this.formBuilder.group(
                        getNotificationScanDetectFilds(true),
                    ),
                }),
            };
        } else {
            return {
                rules: this.formBuilder.group({
                    USER_SCAN_STARTED: this.formBuilder.group(
                        getScriptSchema(),
                    ),
                    CUSTOM_SCAN_STARTED: this.formBuilder.group(
                        getScriptSchema(),
                    ),
                    USER_SCAN_FINISHED: this.formBuilder.group(
                        getScriptSchema(),
                    ),
                    CUSTOM_SCAN_FINISHED: this.formBuilder.group(
                        getScriptSchema(),
                    ),
                    CUSTOM_SCAN_MALWARE_FOUND: this.formBuilder.group(
                        getScriptSchema(),
                    ),
                    USER_SCAN_MALWARE_FOUND: this.formBuilder.group(
                        getScriptSchema(),
                    ),
                }),
            };
        }
    }
}
